import { h, Component } from 'preact';
import { Router } from 'preact-router';

import Home from './routes/home';
import { Provider } from 'preact-redux';
import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import reducers from './reducers';

if (module.hot) {
  require('preact/debug');
}

export default class App extends Component {
  state = {
    createHashHistory: () => null
  };
  /** Gets fired when the route changes.
   *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
   *	@param {string} event.url	The newly routed URL
   */
  handleRoute = e => {
    this.currentUrl = e.url;
  };

  componentDidMount() {
    // dynamically import hash history when on github
    if (process && process.env.PUBLISH_ENV === 'github') {
      import('history/createHashHistory').then(createHashHistory => {
        this.setState({
          createHashHistory
        });
      });
    }
  }

  render() {
    let store = createStore(reducers, applyMiddleware(thunk));

    return (
      <Provider store={store}>
        <div id="app">
          <Router
            history={this.state.createHashHistory()}
            onChange={this.handleRoute}
          >
            <Home path="/:id?/:term?/:mode?" />
          </Router>
        </div>
      </Provider>
    );
  }
}



